* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  h1 {
    margin: 1em;
  }
  
  .login-form {
    align-items: center;
    text-align: center;
    padding: 2em;
    margin-top: 2em;
    margin-left: auto;
    margin-right: auto;
  }
  
  .login-form {
    display: block;
    width: 20em;
    height: fit-content;
  }
  
  .login-form {
    background: linear-gradient(0.30turn, steelblue, lightblue);
    /* border: solid red 0.25em; */
    border-radius: 1em;
    transition: drop-shadow .500ms ease;
  }
  
  .login-form:hover {
    filter: drop-shadow(0.25em 0.25em 0.5em rgba(117, 189, 213, 0.5));
  }
  
  div {
    min-height: 3em;
  }
  
  input {
    text-align: center;
    font-size: 1rem;
    height: 1.5rem;
    border: 0.1em solid black;
    border-radius: 0.5em;
    transition: drop-shadow .500ms ease;
  }
  
  button {
    font-size: 1.5rem;
    padding: 0.25em 0.5em 0.25em 0.5em;
    background-color: white;
    color: black;
    border: 0.1em solid black;
    border-radius: 0.5em;
    transition: drop-shadow .500ms ease;
  }
  
  button:hover {
    filter: drop-shadow(0.1em 0.1em 0.2em rgba(0, 0, 0, 0.25));
  }
  
  form {
    margin: 1.5em;
  }